<template>
    <div>
        <el-card shadow='never'>
            <div slot="header" class="clearfix">
                <span>{{!id?'官网banner新增':'官网banner编辑'}}</span>          
            </div>
            <el-form>
                 <el-form id='form' ref="form" label-position='left' :rules='rules' :model="form" label-width="100px">
                    <el-form-item label="类型:" prop='btype'>
                            <el-radio  v-model="form.btype" label=1>PC</el-radio>
                            <el-radio  v-model="form.btype" label=2>Moblie</el-radio>      
                    </el-form-item>
                    <el-form-item label="标题:" prop='title'>
                            <el-input v-model='form.title'></el-input>     
                    </el-form-item>
                    <el-form-item label="上传图片:" prop='image'>
                        <el-input v-model='form.image' type="hidden"></el-input>  
                    <el-upload
                        class="avatar-uploader"
                        action="/api/admin/file/handle_banner"
                        :show-file-list="false"
                        name='files'
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="image" :src="image" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                  </el-form-item>
                  <el-form-item label="中英文:" prop='lang'>
                            <el-radio  v-model="form.lang" label="cn">中文</el-radio>
                            <el-radio  v-model="form.lang" label="en">英文</el-radio>      
                    </el-form-item>
                   <el-form-item label="链接地址:" prop='url'>
                            <el-input v-model='form.url'></el-input>     
                    </el-form-item>
                    <el-form-item label="权重:" prop='weight'>
                            <el-input v-model='form.weight'></el-input>     
                    </el-form-item>
                  <el-form-item label="是否展示:" prop='isShow'>
                            <el-radio  v-model="form.isShow" :label='true'>是</el-radio>
                            <el-radio  v-model="form.isShow" :label='false'>否</el-radio>      
                    </el-form-item>
                    <el-form-item>
                    <el-button type="primary" v-if='!id' @click="onSubmit">立即提交</el-button>
                    <el-button type="primary" v-else @click="onSubmit">立即修改</el-button>
                    <router-link to='/banner'>
                    <el-button>取消</el-button>
                    </router-link>
                </el-form-item>
                 </el-form>
                  
            </el-form>
        </el-card>


    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name: "banner",
    data() {
        return {
            pctitle:'PC端banner新增',
            mbtitle:'移动端banner新增',
            form:{
                isShow:true,
                btype:'1',
                lang:'cn',
                image:'',
                title:''
            },
            rules:{
                btype: [
                    {
                        required: true,
                        message: "请选择类型",
                        trigger: "blur"
                    }
                ],
                title:[
                    {
                        required: true,
                        message: "请输入标题",
                        trigger: "blur"
                    }
                ],
                lang: [
                    {
                        required: true,
                        message: "请选择语言",
                        trigger: "blur"
                    }
                ],
                image:[
                    {
                        required: true,
                        message: "请选择上传图片",
                        trigger: "blur"
                    }
                ],
                url: [
                    {
                        required: true,
                        message: "请输入url链接",
                        trigger: "blur"
                    }
                ],
                weight:[
                    {
                        required: true,
                        message: "请输入权重",
                        trigger: "blur"
                    }
                ],
            },
            image:'',
            sendUrl:'',
            id:''
        };
    },
    computed: {

    },
    methods: {
        add_banner(){

        },
        handleAvatarSuccess(res, file) {
            this.sendUrl = res.data.url;
            this.image = URL.createObjectURL(file.raw);
            this.form.image = res.data.url;
        },
        onSubmit(){
            let url;
            this.$refs.form.validate(valid => {
                if (!valid) {
                    return;
                }
            let params = {
                notice:this.id?'更新成功！':'新增成功！'
            };
            
            Object.assign(params,this.form);
            if(!this.id){
                url = '/admin/banner/add'
            }else{
                url = '/admin/banner/update'
                Object.assign(params,{
                    id:this.id
                });
            }
            this.$api(url, params).then(res => {
                if(res.code == 0){
                    this.$router.push('/banner');
                }
            });
            })
        },
        onUpdate(){
            
        },
        getBannerDetail(id){
             this.$api("/admin/banner/get",{
                 id:id
             }).then(res => {
                if(res.code == 0){
                    let data = res.data;
                    this.form.image = data.Image;
                    this.image = data.Image;
                    this.form.isShow = data.IsShow;
                    this.form.lang = data.Lang;
                    this.form.title = data.Title;
                    this.form.btype = data.Type+'';
                    this.form.url= data.URL;
                    this.form.weight= data.Weight;
                }
            });
        },
        beforeAvatarUpload(file) {
            const isimg = (file.type == 'image/jpeg' || file.type == 'image/png' ||file.type == 'image/jpg');
            const isLt2M = file.size / 1024 / 1024 < 2;
            if (!isimg) {
            this.$message.error('上传头像图片只能是 JPG 或者 PNG 格式!');
            }
            if (!isLt2M) {
            this.$message.error('上传头像图片大小不能超过 2MB!');
            }
            return isimg && isLt2M;
        }
    },
    created() {
        this.id = this.$route.query.id;
        if(this.id){
            this.getBannerDetail(this.id);
        }
        // this.getFacilityDetail(16)
    }
};
</script>


<style lang='scss' scoped>
.card{
        width: 300px;
        height: 200px;
        img{
                display: block;
    height: 140px;
        }
    }
     .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



